const patientReducer = (state, action) => {
    switch(action.type){

        case 'LOAD_PATIENT_OBJECT':
            return {
                ...state,
                patientObject: action.payload,
            }
        case 'SET_ACTIVE_CONTENT':
            return {
                ...state,
                activeContent: action.payload,
            }
        default:
            return state
    }
}

export default patientReducer;